.glide {
  position: relative;
  width: 100%;
  box-sizing: border-box;
}
.glide * {
  box-sizing: inherit;
}
.glide__track {
  overflow: hidden;
}
.glide__slides {
  position: relative;
  width: 100%;
  list-style: none;
  backface-visibility: hidden;
  transform-style: preserve-3d;
  touch-action: pan-Y;
  overflow: hidden;
  margin: 0;
  padding: 0;
  white-space: nowrap;
  display: flex;
  flex-wrap: nowrap;
  will-change: transform;
}
.glide__slides--dragging {
  user-select: none;
}
.glide__slide {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  white-space: normal;
  user-select: none;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}
.glide__slide a {
  user-select: none;
  -webkit-user-drag: none;
  -moz-user-select: none;
  -ms-user-select: none;
}
.glide__arrows {
  -webkit-touch-callout: none;
  user-select: none;
}
.glide__bullets {
  -webkit-touch-callout: none;
  user-select: none;
}
.glide--rtl {
  direction: rtl;
}

.glide__arrow {
  position: absolute;
  display: block;
  top: 50%;
  z-index: 2;
  color: white;
  text-transform: uppercase;
  padding: 9px 12px;
  background-color: transparent;
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-radius: 4px;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
  text-shadow: 0 0.25em 0.5em rgba(0, 0, 0, 0.1);
  opacity: 1;
  cursor: pointer;
  transition: opacity 150ms ease, border 300ms ease-in-out;
  transform: translateY(-50%);
  line-height: 1;
}
.glide__arrow:focus {
  outline: none;
}
.glide__arrow:hover {
  border-color: white;
}
.glide__arrow--left {
  left: 2em;
}
.glide__arrow--right {
  right: 2em;
}
.glide__arrow--disabled {
  opacity: 0.33;
}
.glide__bullets {
  position: absolute;
  z-index: 2;
  bottom: 2em;
  left: 50%;
  display: inline-flex;
  list-style: none;
  transform: translateX(-50%);
}
.glide__bullet {
  background-color: rgba(255, 255, 255, 0.5);
  width: 9px;
  height: 9px;
  padding: 0;
  border-radius: 50%;
  border: 2px solid transparent;
  transition: all 300ms ease-in-out;
  cursor: pointer;
  line-height: 0;
  box-shadow: 0 0.25em 0.5em 0 rgba(0, 0, 0, 0.1);
  margin: 0 0.25em;
}
.glide__bullet:focus {
  outline: none;
}
.glide__bullet:hover, .glide__bullet:focus {
  border: 2px solid white;
  background-color: rgba(255, 255, 255, 0.5);
}
.glide__bullet--active {
  background-color: white;
}
.glide--swipeable {
  cursor: grab;
  cursor: -moz-grab;
  cursor: -webkit-grab;
}
.glide--dragging {
  cursor: grabbing;
  cursor: -moz-grabbing;
  cursor: -webkit-grabbing;
}

.glide.iucp-upsell-categories-container,
.glide.iucp-upsell-products-container {
  width: 80%;
  margin: auto;
  margin-bottom: 2rem;
}
.glide.iucp-upsell-categories-container .product-container,
.glide.iucp-upsell-products-container .product-container {
  cursor: pointer !important;
  text-align: center;
}
.glide.iucp-upsell-categories-container .product-container *,
.glide.iucp-upsell-products-container .product-container * {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.glide.iucp-upsell-categories-container .product-container .product-image,
.glide.iucp-upsell-products-container .product-container .product-image {
  margin-bottom: 0.5rem;
}
.glide.iucp-upsell-categories-container .product-container .product-image img,
.glide.iucp-upsell-products-container .product-container .product-image img {
  aspect-ratio: 1/1;
  border-radius: 1rem;
}
.glide.iucp-upsell-categories-container .product-container .product-name,
.glide.iucp-upsell-products-container .product-container .product-name {
  font-size: 1.3rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
}
.glide.iucp-upsell-categories-container .product-container .product-price,
.glide.iucp-upsell-products-container .product-container .product-price {
  font-size: 1.2rem;
}
.glide.iucp-upsell-categories-container .product-container .product-button,
.glide.iucp-upsell-products-container .product-container .product-button {
  display: block;
  width: 80%;
  margin: auto;
  background-color: lightblue;
  border-radius: 1rem;
}
.glide.iucp-upsell-categories-container .product-container .product-button a,
.glide.iucp-upsell-products-container .product-container .product-button a {
  color: #fff;
  display: inline-block;
  width: 100%;
  outline: none;
}

body {
  position: relative;
  height: 100%;
  width: 100%;
}
body .iucp-upsell-overlay {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  opacity: 0;
  background-color: #fff;
  transition: all 0.2s;
}
body .iucp-upsell-overlay.active {
  opacity: 0.88;
}

.iucp-products-container > .glide.iucp-upsell-products-container {
  width: 50%;
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 999999;
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  transition: all 0.2s;
}
.iucp-products-container > .glide.iucp-upsell-products-container.active {
  opacity: 1;
  visibility: visible;
}

.glide .glide__arrow--right.iucp {
  right: -3%;
}
.glide .glide__arrow--left.iucp {
  left: -3%;
}